@mixin d {
  @media only screen and (max-width: "1360px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@include font(ProximaNova-Regular, ProximaNova-Regular);
@mixin f {
  font-family: 'ProximaNova-Regular', serif; }
@include font(ProximaNova-Bold, ProximaNova-Bold);
@mixin fb {
  font-family: 'ProximaNova-Bold', serif; }
@include font(ProximaNova-SemiBold, ProximaNova-SemiBold);
@mixin fs {
  font-family: 'ProximaNova-SemiBold', serif; }
@include font(ProximaNova-Light, ProximaNova-Light);
@mixin fl {
  font-family: 'ProximaNova-Light', serif; }

@include font(Majesti-Banner-Bold, Majesti-Banner-Bold);
@mixin fmb {
  font-family: 'Majesti-Banner-Bold', serif; }

@mixin bs {
  box-shadow: 0 2px 5px rgba(black,.4); }
@mixin bs-lg {
  box-shadow: 0 2px 20px 0 rgba(146,146,146,0.50); }

$green: #2ABFBF;
$border: #cecece;
$orange: #F5A623;
$bg: #F5F7F9;
$cl: #6F7C82;
$text: #4A4A4A;

body {
  min-width: 375px;
  @include f;
  font-size: 16px;
  color: $cl;
  line-height: 1.5;
  @include m {
    font-size: 14px; }
  &.no-scroll {
    overflow: hidden; } }
button,
input,
textarea,
select {
  @include f;
  font-size: 16px;
  @include m {
    font-size: 14px; } }
a {
  color: $green;
  text-decoration: none; }
strong,
b {
  @include fs;
  font-weight: 400; }
button {
  background: none; }

* {
 }  // box-shadow: 0 0 0 1px red

[data-animate] {
  opacity: 0;
  visibility: hidden;
  &.animated {
    opacity: 1;
    visibility: visible;
    animation-name: fadeInUp; } }

[data-animate-child] {
  opacity: 0;
  visibility: hidden;
  &.animated {
    opacity: 1;
    visibility: visible;
    animation-name: fadeInUp; } }

.animated {
  animation-duration: .4s;
  animation-color-mode: both;
  &.done {
    animation: none; } }

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 40px, 0); }
  to {
    opacity: 1;
    transform: none; } }

.outer {
  overflow: hidden; }

.bold {
  @include fs; }

.green {
  color: $green; }
.orange {
  color: $orange; }

.center:not(.owl-item) {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 50px;
  @include t {
    padding: 0 30px; }
  @include m {
    padding: 0 15px; }
  &_lg {
    max-width: 1500px; } }

.owl {
  &-dots {
    display: flex;
    margin-top: 30px;
    text-align: center;
    justify-content: center; }
  &-dot {
    width: 10px;
    height: 10px;
    margin: 0 6px;
    background: rgba(black,.1);
    border-radius: 50%;
    transition: background .2s;
    &.active {
      background: $green; } } }

.btn {
  padding: 10px 20px;
  background: $green;
  border: 2px solid transparent;
  border-radius: 22px;
  color: white;
  transition: all .2s;
  line-height: 1.3;
  &:hover {
    background: darken($green,5);
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2), 0 1px 3px rgba(0, 0, 0, 0.05);
    transform: translateY(-1px); }
  &:active {
    box-shadow: none;
    transform: translateY(0); }
  .icon {
    position: relative;
    top: 1px;
    margin-left: 10px;
    font-size: 12px;
    fill: white;
    &:first-child {
      margin: 0 10px 0 0; } }
  &_stroke {
    background: transparent;
    border-color: $green;
    color: $green;
    &:hover {
      background: rgba($green,.1); }
    .icon {
      fill: $green; } }
  &_white {
    border-color: white;
    color: white;
    &:hover {
      background: rgba(white,.2); } }
  &_sm {
    padding: 5px 15px; }
  &_fixed {
    position: fixed;
    right: 50px;
    bottom: 40px;
    z-index: 49;
    @include t {
      right: 30px; }
    @include m {
      display: none; } } }

a.btn {
  display: inline-block; }

.lang {
  position: relative;
  width: 60px;
  @include m {
    position: absolute;
    top: 136px;
    right: 15px; }
  &__head {
    position: relative;
    padding: 5px 20px 5px 10px;
    background: rgba(white,.8);
    border: 1px solid transparent;
    border-radius: 4px;
    color: $cl;
    cursor: default;
    transition: border-color .2s;
    @include m {
      background: #e2e5e6; }
    &:after {
      content: '';
      position: absolute;
      top: 14px;
      right: 10px;
      @include arr(8,6,$cl,b); } }
  &__drop {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    padding-top: 2px;
    opacity: 0;
    visibility: hidden;
    transition: opacity .2s, visibility .2s; }
  &__list {
    padding: 5px 0;
    background: rgba(white,.8);
    border-radius: 4px;
    @include m {
      background: #e2e5e6; } }
  &__item {
    display: block;
    padding: 0 10px;
    color: $cl;
    transition: color .2s;
    &:hover {
      color: $green; } }
  &:hover &__head {
    &:after {
      transform: rotate(180deg); } }
  &:hover &__drop {
    opacity: 1;
    visibility: visible; } }

.header {
  position: relative;
  min-height: 115px;
  z-index: 20;
  @include m {
    min-height: 0; }
  &__container {
    padding: 15px 0;
    transition: background .2s; }
  &__wrap {
    display: flex;
    padding: 54px 0 0 40px;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-end;
    @include t {
      align-items: flex-start; }
    @include m {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      padding: 101px 15px 15px;
      background: white;
      @include bs;
      z-index: 8; } }
  &__menu {
    display: none;
    margin-right: -10px;
    padding: 20px;
    font-size: 0;
    z-index: 10;
    @include m {
      display: block; }
    .icon {
      font-size: 16px;
      fill: white;
      &-close {
        display: none;
        fill: black; } } }
  &__top {
    display: flex;
    position: absolute;
    top: 15px;
    right: 50px;
    margin-bottom: 30px;
    justify-content: flex-end;
    flex-wrap: wrap;
    flex: 0 0 100%;
    @include t {
      right: 30px; }
    @include m {
      position: static;
      justify-content: flex-start; } }
  &__center {
    display: flex;
    @include m {
      align-items: center; } }
  &__contact {
    display: flex;
    margin-right: 20px;
    align-items: center;
    color: white;
    @include m {
      max-width: calc(100% - 80px);
      margin: 0 0 10px;
      padding: 3px 0;
      color: $cl;
      flex: 0 0 100%; } }
  &__icon {
    margin-right: 10px;
    font-size: 0;
    .icon {
      font-size: 20px;
      fill: white;
      @include m {
        fill: $cl; }
      &-email {
        font-size: 18px; } } }
  &__socials {
    display: flex;
    padding-left: 10px;
    border-left: 2px solid white;
    align-items: center;
    @include m {
      position: absolute;
      top: 101px;
      right: 15px; } }
  &__social {
    margin-left: 10px;
    font-size: 0;
    &:hover {
      .icon {
        fill: $green; } }
    .icon {
      font-size: 20px;
      transition: fill .2s;
      @include m {
        fill: $cl; } } }
  &__logo {
    position: relative;
    top: 10px;
    height: 40px;
    margin: 40px auto 0 0;
    font-size: 0;
    flex: 0 0 240px;
    transition: opacity .2s;
    z-index: 10;
    @include t {
      height: 33px;
      flex: 0 0 200px; }
    @include m {
      top: 0; }
    &:before,
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: url(../img/weatherproof-family.svg) no-repeat 0 0 / contain;
      transition: opacity .2s; }
    &:after {
      background: url(../img/weatherproof-family-black.svg) no-repeat 0 0 / contain;
      opacity: 0; }
    @include t {
      margin-top: 40px; }
    @include m {
      margin-top: 0; }
    &:hover {
      opacity: .7; } }
  &__nav {
    display: flex;
    max-width: calc(100% - 60px);
    flex-wrap: wrap;
    @include t {
      padding-top: 5px;
      justify-content: flex-end; }
    @include m {
      display: block;
      max-width: 100%;
      margin: 0;
      padding: 10px 0;
      border-top: 1px solid $border; } }
  &__link {
    margin-right: 30px;
    padding: 5px 0;
    border-bottom: 2px solid transparent;
    @include fs;
    color: white;
    transition: background .2s, color .2s, border-color .2s;
    @include d {
      margin-right: 20px; }
    @include t {
      margin: 0 20px 10px 0;
      padding: 0; }
    @include m {
      display: block;
      margin: 0;
      padding: 10px 15px;
      border: none;
      color: $cl;
      text-align: center; }
    &:hover {
      border-color: $green; }
    &_border {
      padding: 5px 15px;
      border: 1px solid white;
      border-radius: 18px;
      @include t {
        padding: 5px 15px; }
      &:hover {
        background: white;
        border-color: white;
        color: $green;
        @include m {
          color: $cl; } } } }
  &.open &__logo {
    &:before {
      opacity: 0; }
    &:after {
      opacity: 1; } }
  &.open &__wrap {
    @include m {
      display: block; } }
  &.open &__menu {
    @include m {
      .icon {
        display: none;
        &-close {
          display: inline-block; } } } }
  &_black &__logo {
    &:before {
      opacity: 0; }
    &:after {
      opacity: 1; } }
  &_black &__icon,
  &_black &__social {
    .icon {
      fill: $cl; } }
  &_black &__menu {
    .icon {
      fill: black; } }
  &_black &__link,
  &_black &__title {
    color: $cl; }
  &_black &__socials,
  &_black &__link_border,
  &_black .lang__head,
  &_black .lang__list {
    border-color: $cl; } }

@include rmin(769) {
  .header {
    &.fixed &__container {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      background: white;
      @include bs; }
    &.fixed &__logo {
      top: 2px;
      margin: 0 auto 0 0;
      &:before {
        opacity: 0; }
      &:after {
        opacity: 1; } }
    &.fixed &__wrap {
      padding-top: 0; }
    &.fixed &__top {
      display: none; }
    &.fixed &__link {
      color: $cl; }
    &.fixed &__link_border {
      border-color: $cl; }
    &.fixed .lang__head,
    &.fixed .lang__list {
      border-color: $cl; } } }

.call {
  background: white;
  border-radius: 10px;
  color: $cl;
  &__title {
    padding: 10px;
    border-bottom: 1px solid $border;
    @include fs;
    font-size: 12px;
    text-transform: uppercase;
    text-align: center; }
  &__row {
    display: flex; }
  &__item {
    display: flex;
    padding: 10px;
    color: $cl;
    flex: 0 0 45%;
    align-items: center;
    &:last-child {
      flex: 0 0 55%; } }
  &__icon {
    margin-right: 10px;
    font-size: 0;
    .icon {
      font-size: 14px;
      fill: $green; } } }

.scroll {
  position: absolute;
  left: 50%;
  bottom: 100px;
  left: 50%;
  width: 100px;
  margin-left: -50px;
  font-size: 14px;
  color: white;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  @include m {
    display: none; }
  .icon {
    margin-top: 10px;
    font-size: 16px;
    fill: white;
    animation: scroll 1.5s linear infinite; } }

@keyframes scroll {
  0, 50%, 100% {
    transform: translateY(0); }
  25% {
    transform: translateY(25%); }
  75% {
    transform: translateY(-25%); } }



