.photos {
  position: relative;
  width: 100%;
  max-width: 800px;
  @include t {
    max-width: 100%; }
  &:before {
    content: '';
    display: block;
    height: 0;
    padding-bottom: 88.3%; }
  &__item {
    position: absolute;
    box-shadow: 0 2px 20px rgba(#B7B6B6,.5);
    background: white;
    border-radius: 3px;
    opacity: 0;
    transform: translateX(-20%);
    transition: transform .4s, opacity .4s;
    overflow: hidden;
    &:nth-child(1) {
      top: 0;
      left: 3%;
      width: 32%;
      transition-delay: .4s;
      z-index: 1; }
    &:nth-child(2) {
      top: 21%;
      left: 26%;
      width: 66%;
      transition-delay: .2s;
      z-index: 3; }
    &:nth-child(3) {
      top: 58%;
      right: 0;
      width: 38%;
      z-index: 4; }
    &:nth-child(4) {
      left: 0;
      bottom: 0;
      width: 48%;
      transition-delay: .6s;
      z-index: 2; }
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.5) none repeat scroll 0 0;
      transform: scale3d(2.3, 1.4, 1) rotate3d(0, 0, 1, 45deg) translate3d(0px, -100%, 0px);
      transition: transform 0.6s ease 0s;
      z-index: 2; }
    &:hover {
      &:before {
        transform: scale3d(2.3, 1.4, 1) rotate3d(0, 0, 1, 45deg) translate3d(0px, 100%, 0px); } } }
  &__item:hover &__pic {
    opacity: .6;
    transform: scale3d(1.02, 1.02, 1); }
  &__pic {
    display: block;
    width: 100%;
    transform: scale3d(1.0, 1.0, 1);
    transition: opacity 0.35s ease 0s, transform 0.35s ease 0s; }
  &_right &__item {
    transform: translateX(20%); }
  &.visible &__item {
    opacity: 1;
    transform: translateX(0); } }

.projects {
  max-width: 1110px;
  margin: 0 auto;
  &__wrap {
    padding-bottom: 30px;
    @include m {
      padding-bottom: 10px; } }
  &__group {
    position: relative;
    &:before {
      content: '';
      display: block;
      height: 0;
      padding-bottom: 63.064%;
      @include m {
        padding-bottom: 100%; } } }
  &__item {
    position: absolute;
    @include bs;
    color: $cl;
    transition: box-shadow .2s ease-in, transform .2s ease-in;
    overflow: hidden;
    &:nth-child(1) {
      top: 0;
      left: 0;
      width: 47.298%;
      height: 50%;
      @include m {
        width: calc(50% - 5px);
        height: calc(50% - 5px); } }
    &:nth-child(2) {
      top: 0;
      left: calc(47.298% + 30px);
      width: 19.64%;
      height: 36.5715%;
      @include m {
        left: auto;
        right: 0;
        width: calc(50% - 5px);
        height: calc(50% - 5px); } }
    &:nth-child(3) {
      top: 0;
      right: 0;
      width: 27.658%;
      height: 36.5715%;
      @include m {
        top: auto;
        left: 0;
        right: auto;
        bottom: 0;
        width: calc(50% - 5px);
        height: calc(50% - 5px); } }
    &:nth-child(4) {
      left: 0;
      bottom: 0;
      width: 47.298%;
      height: calc(50% - 30px);
      @include m {
        left: auto;
        right: 0;
        width: calc(50% - 5px);
        height: calc(50% - 5px); } }
    &:nth-child(5) {
      right: 0;
