  &__foot {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 10px 40px 10px 25px;
    background: rgba(black,.5);
    opacity: 0;
    transform: translate3d(0,0,0);
    transition: opacity .2s;
    z-index: 2;
